import { Component, h, Element, Prop, State, Listen } from '@stencil/core';

@Component({
  tag: 'ks-order-address-section',
})
export class OrderAddressSection {

    @Prop() heading: string;
    @Prop() link: string;
    @Prop() editString: string;

    @Prop() hideOnMobile: boolean = false;

    @Element() root: HTMLElement;

    @State() mobile: boolean = false;

    @Listen('resize', { target: 'window' })
    resizeHandler() {
      this.mobile = window.innerWidth < 959 ? true : false;
    }

    componentDidLoad(){
        this.root.style.display = "block";
        this.resizeHandler();
    }

    render() {
        const heading = <span class="uk-text-bold ks-text-decorated" style={{fontSize: "22px", textTransform: "uppercase"}}>{this.heading}</span>

        if(this.mobile && this.hideOnMobile ) return (
            <ul uk-accordion>
                <li>
                    <a class="uk-accordion-title" href="#">{heading}</a>
                    <div class="uk-accordion-content">
                        <slot/>

                        <ks-order-button mobile wide href={this.link}><span>{this.editString}</span></ks-order-button>
                    </div>
                </li>
            </ul>
        )

        return[
            <ks-order-heading heading={this.heading} link={this.link}></ks-order-heading>,
            <slot/>
        ];
    }
}